#note {
  height: 100vh;
  width: 100%;
}

.note_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  height: 50px;
  width: 100%;
}

#note_name {
  display: flex;
  align-items: center;
  #note_title_box {
    width: 100%;
    height: 35px;
    border-radius: 10px;
    padding: 5px;
    border: none;
    outline: none;
    font-size: 1.3rem;
    background: none;
    color: inherit;
  }
  #note_title_box:focus {
    border: 2px solid;
  }
}

#note_editor {
  padding: 5px 10px;
  height: calc(100% - 50px);
  width: 100%; 
  display: flex;
  flex-direction: column;
}

#note_content_box {
  background: none;
  color: inherit;
  resize: none;
  width: 100%;
  height: calc(100% - 10px);
  padding: 10px;
  font-size: 1rem;
  border: 1px solid rgba(153, 146, 146, 0.318);
  border-radius: 12px;
  outline: none;
}

#note_content_box:focus {
  border-width: 2px;
}

.current-note-options {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .current-note-status {
    cursor: pointer;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    display: inherit;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    i {
      font-size: 1.4rem;
    }
  }
}

@media (max-width: 550px) {
  #note_name {
    #note_title_box {
      width: 225px;
    }
  }
}
