#manage_account {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0px;
  left: 0px;
  backdrop-filter: blur(10px) saturate(180%);
  -webkit-backdrop-filter: blur(10px) saturate(180%);
}

.ac_form {
  width: 100%;
  height: 100%;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.info_sec {
  padding: 5px 0px;
  .danger {
    color: red;
  }
}

.sec {
  background: rgba(123, 113, 113, 0.094);
}

.userAvatars {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 20px 20px 10px 20px;
}

.avatars {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin: 5px;
}

.selectedAvatar {
  border: 3px solid green;
  padding: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.current-avatar {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: 0.5s ease-in-out;
}

.more_avatar {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  margin-top: 15px;
}

.avatars-category {
  width: 100%;
  // height: 100px;
  padding: 5px 0px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.showMoreBtn {
  width: 100%;
  padding: 10px;
  border-radius: 12px;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(117, 114, 114, 0.275);
  color: inherit;
  cursor: pointer;
  i,
  span {
    color: inherit;
    font-size: 0.9rem;
  }
}

.profile_save_button {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  input {
    height: 40px;
    width: 100%;
  }
}

.selected {
  background: #7d828953 !important;
}

.editable_info {
  // border: 1px solid red;
  width: 100%;
  padding: 20px;
}

#access_div {
  p {
    padding: 5px 0px;
  }
}

#delete_action {
  height: auto;
  margin-top: 10px;
  padding: 0px 20px;
  .section_name {
    span {
      color: Red;
    }
  }
}


#delete-notice{
  padding: 10px 0px;
}

.info{
  margin: 5px 0px;
}

.verifyEmail{
  display: flex;
  justify-content: center;
  align-items: center;
}

#delete_btn {
  cursor: pointer;
  background: rgb(152, 42, 42);
}

@media screen and (max-width: 550px) {
  .avatar_div {
    height: 200px;
    width: 200px;
  }

  #Avatar {
    width: 70%;
    max-height: 70%;
  }

  .avatar_btn {
    width: 30px;
    height: 30px;
  }
}
