#enter_code_p {
  // border: 1px solid rgb(41, 8, 227);
  padding: 2px;
  font-weight: bold;
  text-align: center;
}

#otpForm {
  padding: 10px;
  display: inline-block;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.otp {
  border: 0.2px solid rgb(94, 96, 95);
  width: 38px;
  height: 38px;
  font-size: 1.8rem;
  font-weight: 600;
  padding-left: 10px;
  margin: 6px;
  outline: none;
  box-shadow: 0px 0px 2px 1px rgba(42, 43, 43, 0.786);
  background: rgba(201, 193, 193, 0.544);
  border-radius: 10px;
}

.otp:focus {
  border: 3px solid rgba(7, 129, 222, 0.65);
  box-shadow: 0px 0px 1px 1px rgba(7, 129, 222, 0.65);
}
.otp:hover {
  border: 3px solid rgba(35, 152, 241, 0.543);
  box-shadow: 0px 0px 1px 1px rgba(7, 129, 222, 0.65);
}

@media (max-width: 520px) {
  #otpForm {
    padding: 10px 0px;
  }
  .otp {
    width: 30px;
    height: 30px;
    font-size: 1.5rem;
    margin: 5px;
  }
}
