.edit_verification {
  min-width: 30%;
  min-height: 350px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
}

.resend_code {
  width: 80%;
  height: 35px;
  color: rgb(0, 101, 253);
  font-family: monospace, Arial, Helvetica, sans-serif;
  animation: fade 2s ease 0s backwards both;
  border: none;
  outline: none;
  background: transparent;
  font-size: 1em;
  cursor: pointer;
}

.resend_code:hover {
  text-decoration: underline;
}

@keyframes fade {
  0% {
    width: 1%;
    opacity: 0;
  }
  100% {
    width: 80%;
    opacity: 0.9;
  }
}

.verify_changes {
  min-height: 200px;
  padding:15px 5px; 
  display: inline-block;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around; 
}

button:disabled {
  color: rgba(76, 76, 76, 0.502);
  text-decoration: underline;
}

.otp {
  border: none;
  width: 40px;
  height: 40px;
  padding-left: 10px;
  border-radius: 10px;
  font-size: 1.8rem;
  font-weight: 600;
  margin: 6px;
  outline: none;
  background: rgba(201, 193, 193, 0.544);
}

.otp:focus {
  border: 3px solid rgba(7, 129, 222, 0.65);
  box-shadow: 0px 0px 1px 1px rgba(7, 129, 222, 0.65);
}
.otp:hover {
  border: 3px solid rgba(7, 129, 222, 0.65);
  box-shadow: 0px 0px 1px 1px rgba(7, 129, 222, 0.65);
}

.btnsToVerify {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.verification_btn {
  height: 35px;
  width: 150px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border-radius: 10px;
  font-weight: 500;
  margin: 5px;
  border: none;
  transition: 0.3s ease;
  font-size: 0.9rem;
  cursor: pointer;
}

#submitCode {
  background: rgb(0, 145, 255);
}

#submitCode:hover {
  background: rgba(1, 145, 255, 0.77);
}

#resendCode {
  background: rgb(1, 213, 1);
}

#resendCode:hover {
  background: rgba(4, 204, 4, 0.702);
}

#cancelVerification {
  border: none;
  background: transparent;
  color: #0e93f8;
  font-size: 0.9em;
  text-decoration: dashed;
  padding: 5px;
  cursor: pointer;
}

#cancelVerification:hover {
  text-decoration: underline;
}

@media (max-width: 520px) {
  .edit_verification {
    width: 95%;
    min-height: 350px;
  }

  #otpForm,
  #submitOtp {
    padding: 10px;
  }

  .otp {
    width: 35px;
    height: 35px;
  }
}
