#settings {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0px;
  left: 0px;
  backdrop-filter: blur(10px) saturate(180%);
  -webkit-backdrop-filter: blur(10px) saturate(180%);
}


.s_item {
  //  border: 1px solid rgb(89, 0, 255);
  margin-top: 10px;
  background: #8488902a;
}

.section_name {
  // border: 1px solid red;
  padding: 10px;
  span {
    font-size: 1.1rem;
    font-weight: bold;
  }
}

.small_header {
  text-align: start;
  padding: 5px 10px;
  background: #8488902a;
  font-size: 1rem;
}

#dark_mode_category {
  width: 100%;
  padding: 10px 0px;
  background: #8488902a;
  display: flex;
  justify-content: space-around;
}

.dark_mode_options {
  box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px, rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
  cursor: pointer;
  width: 22%;
  border-radius: 5px;
  text-align: center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: rgba(59, 56, 56, 0.46);
  input {
    display: none;
  }
  label {
    width: 100%;
    height: 100%;
    padding: 5px 0px;
    display: inherit;
    align-items: inherit;
    justify-content: inherit;
    font-weight: 300;
    cursor: pointer;
  }
}

.selected_options {
  background: rgba(11, 244, 23, 0.666);
}

.wide-list-btn {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin: 5px 0px;
  label {
    display: block;
    width: 100%;
    padding: 10px;
    color: inherit;
    span {
      font-size: 0.8em;
    }
  }
  input {
    position: absolute;
    right: 1%;
  }
}

.feature_available {
  background: #8488902a;
  cursor: pointer;
  color: inherit;
}

.security_form { 
  height: 100%;
  color: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 10px;

  input[type="submit"] {
    margin: 10px;
  }
}

.security_input_div {
  width: 100%;
  height: 100%;
}

.submit_btn_div {
  display: block;
  width: 100%;
  text-align: right;
}

.about_div {
  display: flex;
  padding: 10px;
  img {
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }
  .about_info {
    p {
      margin-bottom: 5px;
      font-size: 0.9rem;
    }
  }
}
#show_password {
  //   border: 2px solid green;
  width: 100%;
  padding: 4px 0px;
  #show_pwd_btn {
    color: inherit;
    padding: 5px 5px;
    width: inherit;
    outline: none;
    border: none;
    background: rgba(98, 90, 90, 0.355);
    cursor: pointer;
    border-radius: 5px;
    span {
      color: inherit;
      font-family: monospace, system-ui, -apple-system;
    }
  }
}


#help_form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
  padding: 10px; 

  textarea {
    border-radius: 10px;
    background: inherit;
    color: inherit;
    outline: none;
    padding: 10px;
    min-height: 30%;
    border: 2px solid rgba(118, 113, 113, 0.644);
    resize: none;
  }

  textarea:focus {
    border: 2px solid rgb(0, 145, 255);
  }
  input {
    width: fit-content;
    margin: 10px 0px 10px auto;
  }
}

 