@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;700&family=M+PLUS+Rounded+1c:wght@500&family=Orbitron&family=Quantico&family=Teko:wght@300&display=swap");

* {
  font-family: Comfortaa, monospace, Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  text-decoration: none;
  overflow: auto;
  margin: 0px;
  padding: 0px;
}

*::-webkit-scrollbar {
  width: 16px;
}

*::-webkit-scrollbar-track {
  background-color: none;
  border-radius: 5px;
}

*::-webkit-scrollbar-thumb {
  cursor: pointer;
  border-radius: 10px;
  border: 5px solid transparent;
  background-clip: content-box;
  background-color: #514d65;
}

.App {
  transition: 0.5s ease-in-out;
}

.dark {
  background: #000000;
  color: #eceeff;
  .background_component {
    background: #1e1d1d;
  }
}

.light {
  background: #eceeff;
  color: #000000;
  .background_component {
    background: #eceeff;
  }
}

/// auth css

.auth-container {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth-box {
  position: relative;
  height: auto;
  //
}

.authHeader {
  position: relative;
  width: 100%;
  height: 140px;
  #appNameAndLogo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    img {
      display: inline-block;
      width: 50px;
      height: 50px;
      margin: 5px;
      border-radius: 50%;
      vertical-align: auto;
    }
    h1 {
      font-weight: bolder;
      margin: 0px;
      display: inline-block;
    }
  }
  p {
    font-size: 1.3rem;
    font-weight: bold;
    margin: 10px 0px 0px 3px;
    text-align: center;
  }
}

#signin {
  max-height: 80%;
  width: 35%;
}

#signup {
  max-height: 95%;
  min-width: 30%;
  width: 45%;
}

.authInputBox {
  position: relative;
  width: 100%;
  height: auto;
}

.auth-form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  input:focus {
    border: 2px solid rgb(6, 126, 218);
  }
  ::placeholder {
    color: #a29b9b;
    font-size: 1rem;
    font-weight: 600;
  }
}

.firstAndLast_name {
  display: flex;
  justify-content: space-between;
  .input_wrapper {
    width: 49.1%;
    display: inline-block;
  }
}

.input_wrapper {
  position: relative;
  height: auto;
  margin: 0px !important;
  // border: 1px solid rgb(216, 1, 23) !important;

  label {
    padding: 0px;
    position: relative;
    width: 100%;
    height: auto;
  }

  input {
    width: 100%;
    padding: 0px 5px;
    margin-top: 22px;
    border-radius: 5px;
    border: 0.2px solid rgb(74, 72, 75);
    outline: none;
    height: 35px;
    font-weight: bold;
    background: inherit;
    color: inherit;
  }

  input:focus {
    border: 2px solid rgb(6, 126, 218);
  }

  input::placeholder {
    opacity: 0;
  }
  .spanPlaceholder,
  .birthdayPlaceholder {
    position: absolute;
    top: -20px;
    left: 5px;
    transform: translateY(20px);
    font-size: 1em;
    font-weight: bold;
    transition-duration: 300ms;
    color: #948e8e;
  }

  .birthdayPlaceholder {
    opacity: 0;
  }

  label:focus-within > .spanPlaceholder,
  input:not(:placeholder-shown) + .spanPlaceholder {
    color: inherit;
    transform: translateY(-5px);
    font-size: 0.8em;
  }
}

.input_error {
  border: 2px solid rgb(255, 0, 0);
}

#forgotPassword {
  text-align: right;
  position: relative;
  padding: 5px;

  .link {
    font-weight: bold;
    color: #3e9cf2;
    text-decoration: underline;
  }
}

#signup_Btn,
#signin_Btn {
  font-weight: bold;
}

.inputsBtn {
  height: 130px;
  display: flex;
  align-items: end;
  justify-content: space-between;
}

.next {
  max-width: 60%;

  span {
    word-wrap: break-word;
  }
}

.next a {
  color: #3e9cf2;
  text-decoration: underline;
}

.continueBtn {
  border-radius: 5px;
  padding: 5px;
  height: 35px;
  display: inline-block;
  width: 85px;
  border: none !important;
  background: #3e9cf2;
  font-weight: bold;
  cursor: pointer;
  font-size: 1rem;
  color: inherit;
}

#AppLoading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background: inherit;
  img {
    width: 192px;
    height: auto;
    animation: rotate 1s ease-in-out;
  }
  h1 {
    color: inherit;
    margin: 5px;
    font-weight: 100;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 950px) {
  #AppLoading {
    img {
      width: 250px;
      height: 250px;
    }
  }

  #signin {
    max-height: 70%;
    width: 50%;
  }

  #signup {
    max-height: 95%;
    width: 60%;
  }

  .authHeader {
    height: auto;
    min-height: 100px;
    img {
      width: 50px;
      height: 50px;
    }
    p {
      font-size: 1rem;
      margin-top: 5px;
    }
  }
  .inputsBtn {
    height: 110px;
  }
}

@media (max-width: 520px) {
  #AppLoading {
    img {
      width: 150px;
      height: 150px;
    }
    h1 {
      font-size: 1.5rem;
    }
  }
  #signin {
    max-height: 70%;
    width: 95%;
  }

  #signup {
    max-height: 95%;
    width: 95%;
  }
  .authHeader {
    height: auto;
    min-height: 100px;
    img {
      width: 45px;
      height: 45px;
    }
  }
  .inputsBtn {
    height: 90px;
  }

  .next {
    p,
    input {
      font-size: 0.8rem;
    }
  }
}

#timer {
  text-align: center;
  border-radius: 5px;
  width: 100%;
  word-wrap: break-word;
  min-height: 30px;
  padding: 5px 5px 0px 5px;
  span {
    font-weight: 600;
    width: inherit;
    display: inline-block;
  }
  animation: fade 1s linear 0s backwards both;
}

.ERROR {
  padding: 0px 3px;
  width: 100%;
  height: auto;
  overflow: auto;
  div {
    i {
      margin-right: 5px;
      color: red;
      font-size: 0.8rem;
      vertical-align: sub;
      padding: 1px;
    }
    span {
      font-size: 0.9rem;
      color: red;
      word-wrap: break-word;
      font-family: monospace;
      margin: 0px !important;
    }
  }
}

.window {
  z-index: 999;
  border: 0.5px solid rgba(47, 45, 45, 0.303);
  box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px,
    rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
  min-height: 70%;
  max-height: 90%;
  width: 45%;
  border-radius: 12px;
  padding: 10px 0px;
}

@media (max-width: 1150px) {
  .window {
    width: 70%;
  }
}

@media (max-width: 550px) {
  .window {
    border-radius: 0px;
    width: 100%;
    max-height: 100%;
  }
}

/////////////////////////////////////////////////

.option_btn {
  background: none;
  outline: none;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  color: inherit;
  padding: 0px;
  display: inline-block;
  i {
    color: inherit;
    font-size: 1.3rem;
  }
}

.option_btn:nth-child(1) {
  margin-right: 5px;
}

.back_btn {
  border-radius: 50%;
  background: none;
  outline: none;
  border: none;
  height: 35px;
  width: 35px;
  padding: 0px;
  vertical-align: middle;
  background: none;
  color: inherit;
  cursor: pointer;
  i {
    color: inherit;
    font-size: 1.3rem;
  }
}

.hoverIt:hover {
  background: rgba(241, 241, 241, 0.254) !important;
}

.scaleIt:hover {
  transition: 0.2s ease-in-out;
  transform: scale(1.2) !important;
}

.pressIt:hover {
  transition: 0.2s ease-in-out;
  transform: scale(0.9) !important;
}

.toggle_switch {
  --s: 20px; /* adjust this to control the size*/
  height: calc(var(--s) + var(--s) / 5);
  width: auto; /* some browsers need this */
  aspect-ratio: 2.25;
  border-radius: var(--s);
  display: grid;
  cursor: pointer;
  background-color: #9d9292;
  box-sizing: content-box;
  overflow: hidden;
  transition: 0.3s 0.1s;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.toggle_switch:before {
  content: "";
  padding: calc(var(--s) / 10);
  --_g: radial-gradient(
    circle closest-side at calc(100% - var(--s) / 2) 50%,
    #000 96%,
    #0000
  );
  background: var(--_g) 0 / var(--_p, var(--s)) 100% no-repeat content-box,
    var(--_g) var(--_p, 0) / var(--s) 100% no-repeat content-box, #fff;
  mix-blend-mode: darken;
  filter: blur(calc(var(--s) / 12)) contrast(11);
  transition: 0.4s, background-position 0.4s 0.1s,
    padding
      cubic-bezier(0, calc(var(--_i, -1) * 200), 1, calc(var(--_i, -1) * 200))
      0.25s 0.1s;
}
.toggle_switch:checked {
  background-color: #85ff7a;
}
.toggle_switch:checked:before {
  padding: calc(var(--s) / 10 + 0.05px) calc(var(--s) / 10);
  --_p: 100%;
  --_i: 1;
}

.myBtn {
  outline: none;
  border: none;
  border-radius: 5px;
  background: #0a76f1;
  color: inherit;
  transition: 0.2s ease;
  padding: 5px 10px;
  text-decoration: none;
  font-size: 1rem;
  cursor: pointer;
}

.nav-dropdown {
  color: inherit;
  height: auto;
  width: fit-content;
  z-index: 21;
  position: absolute;
  top: 55px;
  right: 10px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 12px;
  max-width: 220px;
  box-shadow: 0px 0px 2px 2px rgba(105, 103, 103, 0.462);

  .nav-dropdown-menu {
    border-radius: 12px;
    width: 100%;
    height: 100%;
    padding: 0px;
    margin: 0px;
    list-style: none;
    backdrop-filter: blur(50px) saturate(200%);
    -webkit-backdrop-filter: blur(10px) saturate(50%);
    background-color: rgba(255, 255, 255, 0.349);

    .colorContainer {
      display: flex;
      flex-wrap: wrap;
      padding: 5px;

      .colorCode {
        border-radius: 50%;
        width: 25px;
        height: 25px;
        margin: 5px !important  ;
      }
    }

    .current-user {
      border-top: none;
      text-align: center;
      font-weight: bold;
      font-size: 0.9em;
      line-height: 1.5;
      padding: 5px;
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
    }
    li {
      word-wrap: break-word;
      border-top: 1px solid rgba(25, 24, 24, 0.263);
      line-height: 0;
      list-style: none;
      a,
      button {
        display: inline-block;
        width: 100%;
        height: 100%;
        padding: 10px;
        margin: 0px !important;
        text-decoration: none;
        color: inherit;
        font-size: 1em;
        i {
          padding: 2px;
          vertical-align: sub;
          margin-right: 5px;
        }
      }
      button {
        cursor: pointer;
        outline: none;
        background: none;
        text-align: start;
        border: transparent;
      }
    }
  }
}

#alert_container {
  // border: 2px solid red;
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
  height: auto;
  left: 0px;
  bottom: 15%;
}

.red_alert {
  border: 2px solid #ff0000;
}
.green_alert {
  border: 2px solid #008000;
}

#Alert {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  min-width: 10%;
  max-width: 80%;
  word-wrap: break-word;
  border-radius: 10px;
  font-weight: bold;
  svg {
    margin-right: 5px;
  }
  span {
    font-size: 0.8em;
  }
}

.hide {
  display: none !important;
}

.mySvg {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  padding: 0px 20px;
  svg {
    width: 100%;
    height: 80%;
  }
}

.disabled_btn {
  cursor: not-allowed;
  background: #47474784;
}

