#navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 10px;
  height: 45px;
}

#app_name_and_logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 5px;
  border-radius: 12px;
}

#nav_options {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hideOptions {
  width: fit-content;
  .options {
    display: none;
  }
}

.showOptions {
  width: fit-content;
}

.fadeInOut-anim {
  overflow: hidden;
  animation: fadeInOut 0.3s ease;
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    max-height: 0;
  }
  100% {
    opacity: 1;
    max-height: 300px; /* Adjust to your content height */
  }
}
