#home {
  height: 100vh;
  width: 100%;
  position: relative;
  background: inherit;
}



#quote{
  border: 1px solid red;
  margin: 10px 0px;
  text-align: center;
}



.my_notes {
  height: calc(100% - 45px);
}

.notes-list {
  // border: 1px solid rgb(0, 128, 32);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 5px 0px;

  .notes_item {
    min-height: 60px;
    width: 100%;
    list-style: none;
    overflow: hidden;
    padding: 0px 5px;
  }

  .saved_note {
    border: 1px solid rgba(93, 95, 97, 0.191);
    border-left: none;
    border-right: none;
    border-top: none;
    width: 100%;
    height: 100%;
    list-style: none;
    display: flex;
    cursor: pointer;
    text-decoration: none;
    color: inherit;
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 10px;
  }

  .saved_note:hover {
    border-radius: 12px;
  }

  .color_time {
    display: flex;
    flex-direction: column;
    align-items: end;
    justify-content: space-evenly;
    span {
      font-size: 0.9rem;
    }
    .colorCode {
      height: 10px;
      width: 10px;
      border-radius: 50%;
    }
  }

  .title_content {
    overflow: hidden;
    width: 60%;

    .note_content {
      margin-top: 2px;
      height: 20px;
      overflow: hidden;
      font-size: 0.9rem;
    }
  }
}

.notes-grid {
  // border: 1px solid rgb(0, 2, 128);
  width: 100%;
  height: auto;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;

  .notes_item {
    width: 25%;
    min-width: 200px;
    height: auto;
    min-height: 320px;
    list-style: none;
    padding: 5px;
  }

  .saved_note {
    box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px,
      rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
    background: rgba(183, 179, 179, 0.164);
    border: 2px solid rgba(74, 71, 71, 0.247);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    text-decoration: none;
    color: inherit;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-end;
    margin: 0px;
    transition: 0.5s ease;
  }

  .saved_note:hover {
    transform: scale(0.95);
  }

  .color_time {
    text-align: end;
    padding: 5px 10px;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    span {
      font-size: 0.8rem;
    }
    .colorCode {
      height: 10px;
      width: 10px;
      border-radius: 50%;
      margin-right: 5px;
    }
  }

  .title_content {
    // border: 1px solid rgb(13, 0, 195);
    overflow: hidden;
    padding: 5px;
    max-height: 80%;
    .note_content {
      line-height: 1.45;
      word-wrap: break-word;
    }
  }
}

@media (max-width: 1250px) {
  .notes-grid {
    .notes_item {
      width: 33.3%;
      min-width: 200px;
    }
  }
}

@media (max-width: 950px) {
  .notes-grid {
    .notes_item {
      width: 33.3%;
      min-width: 200px;
      min-height: 250px;
      max-height: 275px;
    }
  }
}

@media (max-width: 750px) {
  .notes-grid {
    .notes_item {
      width: 50%;
      min-width: 150px;
    }
  }
}

@media (max-width: 550px) {
  .notes-grid {
    .notes_item {
      width: 50%;
      min-width: 100px;
      min-height: 200px;
      max-height: 200px;

      .color_time {
        span {
          font-size: 0.6rem;
        }
        .colorCode {
          height: 8px;
          width: 8px;
        }
      }
      .title_content {
        max-height: 80%;
        .note_title {
          font-size: 1.1rem;
        }
        .note_content {
          font-size: 0.9rem;
          line-height: 1.2;
        }
      }
    }
  }

  .notes-list {
    .color_time {
      span {
        font-size: 0.5rem;
      }
      .colorCode {
        height: 8px;
        width: 8px;
      }
    }
    .title_content {
      .note_title {
        font-size: 1.1rem;
      }
      .note_content {
        font-size: 0.9rem;
        line-height: 1.2;
      }
    }
  }
}
