.recover-container {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.reset {
  position: relative;
  min-height: 30%;
  min-width: 30%;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 10px;
  padding: 10px;
  z-index: 5;
  .resetPassword {
    h1 {
      font-weight: 100;
      margin: 10px 0px;
    }

    p {
      margin: 5px 0px;
    }

    img {
      width: 60px;
      height: 60px;
    }
    margin: 10px 0px;
    text-align: center;
  }
  .email_success {
    h2 {
      margin: 10px 0px;
    }
    p {
      margin: 3px 0px;
    }
  }
  .goto {
    margin: 10px 0px;
    i {
      margin: 0px 10px;
      vertical-align: middle;
    }
  }

  form {
    width: 100%;
    input[type="email"],
    input[type="password"] {
      background: inherit;
      color: inherit;
      border-radius: 5px;
      border: 0.2px solid rgb(74, 72, 75);
      outline: none;
      width: 100%;
      height: 40px;
      padding: 0px 5px;
      font-weight: bold;
      font-size: 1rem;
    }
    input[type="password"] {
      margin: 10px 0px;
    }
    div {
      margin-bottom: 5px;
    }
    #submitEmail {
      float: right;
    }
  }
}

@media (max-width: 950px) {
  .reset {
    min-height: 25%;
  }
}
