#verification-Box {
  min-height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  transition: 0.3s linear;
  padding: 10px; 
}

.verify_email {
  text-align: center;
  font-weight: bold;
  color: inherit; 
  height: 10%;
}

#verify-illustration {
  height: 25%;
}

#hint {
  border: 0.5px solid rgba(20, 156, 20, 0.584);
  word-wrap: break-word;
  min-height: 30px;
  padding: 5px;
  font-weight: 600;
  background: #5aab6d39;
  border-radius: 10px;
  color: rgb(2, 214, 2);
  text-align: center;
  p {
    margin: 0px;
  }
}

.verificaion-input-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
  height: 60%;
  transition: 4s linear;
}

.form_btns {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-buttons {
  height: 45px;
  width: 200px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border-radius: 10px;
  font-weight: 500;
  margin: 5px;
  border: none;
  transition: 0.3s ease;
  font-size: 1rem;
  cursor: pointer;
}

#submit {
  background: rgb(0, 136, 240);
}

#submit:hover {
  background: rgb(25, 154, 252);
}

#resend {
  background: rgb(2, 156, 2);
}

#resend:hover {
  background: rgb(1, 234, 1);
}

#cancel {
  border: none;
  background: transparent;
  color: #0e93f8;
  font-size: 0.9em;
  text-decoration: dashed;
  margin: 0px;
  cursor: pointer;
}

#cancel:hover {
  text-decoration: underline;
}

@media (max-width: 520px) {
  .input-buttons {
    height: 40px;
    width: 150px;
  }
}
